import React from 'react';
import style from './kitchen.module.scss';

import Swipe from '../../common/swipe/swipe'

class Kitchen extends React.Component{
    state={
        msg:[]
    
    }
    componentWillMount(){
        fetch(
            `/data/kitchen.json`
          ).then(
            res=>res.json()
            // res=>res.text()
          ).then(
            data=>{this.setState({msg:data})}
            
          )
    }
    render(){
        return(
            <div className = {style.kitchenWarp}>
                <h2 style={{textAlign : "center",fontSize : "20px"}}>VIP会员</h2>
                <div>

                    <Swipe imgs = {[
                                {src : "http://img5.imgtn.bdimg.com/it/u=1607036595,2950630734&fm=26&gp=0.jpg",width : "90vw",height : "20vh"},
                                {src : "http://img4.imgtn.bdimg.com/it/u=1440258767,292683893&fm=26&gp=0.jpg",width : "90vw",height : "20vh"},
                                {src : "http://img5.imgtn.bdimg.com/it/u=1607036595,2950630734&fm=26&gp=0.jpg",width : "90vw",height : "20vh"},
                                ]} 
                    />

                </div>

                <div className={style.kitchen}>
                <ul className={style.ul1}>
                    {
                        this.state.msg.map((item,index)=>(
                            <li className={style.li1} key={item.id}>
                        <h3>{item.title}</h3>
                        <ul className={style.ul2}>
                            <li><img src={item.src1}/><span>{item.name1}</span></li>
                            <li><img src={item.src2}/><span>{item.name2}</span></li>
                        </ul>
                    </li>
                        ))
                    }    
                </ul>
                </div>
            </div>
        )
    }
}


export default Kitchen;